<template>
	<div class="eqtest-container">
		<div class="eqtest-header">
			<div class="eqtest-zhong">
				<div class="eqtest-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="eqtest-ti">情商测试</div>
				<div></div>
			</div>
		</div>
		<div class="eqtest-content">
			<div v-for="item in eqtests_list">
				<div class="eqtest-border">
					<div class="eqtest-flex">
						<div class="eqtest-tu">
							<!--<img src="../assets/img_qscs.png">-->
							<img :src="!!item.eqtest_pic?baseUrl+item.eqtest_pic:'../assets/img_qscs.png'">
						</div>
						<div @click="go_testdetails(item.eqtest_id)">
							<div class="eqtest-xiang">{{item.eqtest_title}}</div>
							<!--<div class="eqtest-xiang">{{item.eqtest_content}}</div>-->
							<div class="eqtest-mianfei" @click="go_testdetails(item.eqtest_id)">免费</div>
						</div>
					</div>
				</div>
			</div>

			<!--<div class="eqtest-border">
				<div class="eqtest-flex">
					<div class="eqtest-tu">
						<img src="../assets/img_qscs.png">
					</div>
					<div>
						<div class="eqtest-xiang">女生聊天说这几句话，代表她想和你谈恋爱了</div>
						<div class="eqtest-huiyuan">会员免费</div>
					</div>
				</div>
			</div>-->
		</div>
		<div class="eqtest-footer">

		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: [],
				eqtests_list: ''
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			go_testdetails(id){
				this.$router.push('/testdetails/' + id);
			},
			eqtests() {
				var that = this;
				that.$http.post('/eqtest/eqtests', {}).then((res) => {
					if(res.meta.code == 200) {
						that.eqtests_list = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			this.eqtests();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.eqtest-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.eqtest-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.eqtest-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.eqtest-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.eqtest-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.eqtest-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.eqtest-border {
		border: 1px solid #FFFFFF;
		background: #FFFFFF;
		margin-bottom: 0.5rem;
	}
	
	.eqtest-flex {
		display: flex;
	}
	
	.eqtest-tu img {
		width: 10rem;
		height: 10rem;
		margin: 1rem 2.5rem 0.7rem 1.5rem;
	}
	
	.eqtest-xiang {
		font-size: 1.4rem;
		color: #000000;
		padding-top: 1rem;
		padding-right: 1rem;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		/*white-space: nowrap;*/
	}
	
	.eqtest-mianfei {
		border: 1px solid #62a55e;
		background: #62A55E;
		font-size: 1.2rem;
		border-radius: 0.3rem;
		color: #FFFFFF;
		width: 70px;
		text-align: center;
		margin-top: 4rem;
		margin-bottom: 0.5rem;
	}
	
	.eqtest-huiyuan {
		border: 1px solid #8CADF5;
		background: #8CADF5;
		font-size: 1.2rem;
		border-radius: 0.3rem;
		color: #FFFFFF;
		width: 70px;
		text-align: center;
		margin-top: 1rem;
	}
</style>


